<style lang="less" scoped>
//遗忘密码
@import "../css/common.less";
.forget {
  .back {
    color: @appColor;
    img {
      width: unit(88/@rem, rem);
      height: unit(88/@rem, rem);
    }
  }
  //验证码登录
  .login-code {
    padding: 0 unit(58/@rem, rem);
    position: relative;
    .form-item {
      border-bottom: 1px solid @borderColor;
      .phone-code {
        flex: 0 0 auto;
      }
      .change-show {
        flex: 0 0 auto;
        padding: 0 unit(10/@rem, rem);
        height: unit(40/@rem, rem);
        width: unit(40/@rem, rem);
      }
      .img {
        height: unit(40/@rem, rem);
        width: unit(40/@rem, rem);
      }
      .forget-pwd {
        color: @txt-lv3;
        position: absolute;
        right: unit(58/@rem, rem);
        bottom: unit(-70/@rem, rem);
      }
    }
  }
  //登录按钮
  .submit {
    margin: 0 unit(58/@rem, rem);
    margin-top: unit(105/@rem, rem);
    margin-bottom: unit(30/@rem, rem);
    text-align: center;
    height: unit(98/@rem, rem);
    line-height: unit(98/@rem, rem);
  }
}
</style>
<template>
  <div class="forget">
    <anyiHeader title="验证手机">
      <div @click="$back" class="back fz-48" slot="left">
        <img src="../assets/back.png">
      </div>
    </anyiHeader>
    <!-- 验证码登录 -->
    <div class="login-code">
      <div class="form-item">
        <anyiInput :maxLength="11" type="number" placeholder="请输入手机号" v-model="formData.phone" />
      </div>
      <div class="form-item-verifyCode form-item flex row space item-center">
        <anyiInput :maxLength="4" type="text" placeholder="请输验证码" v-model="formData.verifyCode" />
        <!-- 图形验证码 -->
        <div @click="getCaptcha(true)" class="svg-img flex center" v-html="captchaImg">
        </div>
      </div>
      <div class="form-item flex row space item-center">
        <anyiInput :maxLength="4" type="number" placeholder="请输入手机验证码" v-model="formData.code" />
        <!-- 获取手机验证码 -->
        <div @click="getCode" class="phone-code anyi-btn-txt fz-32" :class="{'anyi-btn-txt-disabled':codeDisabled || txtDisaled}">
          {{txtConfig.getCodeTxt}}
        </div>
      </div>
    </div>
    <!-- 下一步按钮 -->
    <div @click="nextStep" class="anyi-btn submit fz-36" :class="{'anyi-btn-disabled': btnDisabled}">
      {{txtConfig.formType}}
    </div>

  </div>
</template>
<script>
import { Toast } from "mint-ui";
import anyiHeader from "../components/common/header";
import anyiInput from "../components/common/anyiInput";
export default {
  components: {
    anyiInput,
    anyiHeader
  },
  data() {
    return {
      //表单参数对象
      formData: {
        phone: "", //手机号
        verifyCode: "", //图形验证码
        code: "" //手机验证码
      },
      //文案对象
      txtConfig: {
        getCodeTxt: "获取验证码",
        formType: "下一步"
      },
      codeDisabled: false,
      captchaImg: "",
      timer: ""
    };
  },
  computed: {
    //获取手机验证码是否禁用
    txtDisaled() {
      if (
        this.formData.verifyCode.length < 4 ||
        this.formData.phone.length < 11
      ) {
        return true;
      }
      return false;
    },
    //登录按钮是否禁用
    btnDisabled() {
      if (
        this.formData.phone.length < 11 ||
        this.formData.verifyCode.length < 4 ||
        this.formData.code.length < 4
      ) {
        return true;
      }
      return false;
    }
  },
  mounted() {
    //获取图形验证码
    this.getCaptcha(true);
  },
  methods: {
    //获取验证码
    getCode() {
      //校验手机号码 获取验证码后重新刷新一下图形验证码 但是不显示
      if (this.txtDisaled) return;
      this.getCaptcha();
      var ruls = [
        this.$valid.ruls.phone(this.formData.phone),
        this.$valid.ruls.verifyCode(this.formData.verifyCode)
      ];
      if (!this.$valid.validate(ruls)) return;
      if (!this.codeDisabled) {
        this.$http
          .getCode({
            data: {
              phone: this.formData.phone,
              verifyCode: this.formData.verifyCode
            }
          })
          .then(
            res => {
              this.setBtnTxt();
              //点击成功后 获取ajax 获取code
              Toast({
                message: "验证码已发送至您手机",
                position: "center",
                duration: 2000
              });
            },
            err => {}
          );
      }
    },
    //获取图形验证码
    getCaptcha(params) {
      //传入true未显示 不传或false 不显示
      this.$http.getCaptcha().then(res => {
        if (params) {
          this.captchaImg = res.result.svg;
        }
      });
    },
    //动态设置按钮文案
    setBtnTxt() {
      //清除时钟
      clearInterval(timer);
      this.codeDisabled = true;
      var index = 60;
      this.txtConfig.getCodeTxt = index + "秒后重新获取";
      var timer = setInterval(() => {
        index--;
        //动态设置文案
        this.txtConfig.getCodeTxt = index + "秒后重新获取";
        if (index <= 0) {
          //还原文案
          this.txtConfig.getCodeTxt = "获取验证码";
          this.codeDisabled = false;
          clearInterval(timer);
        }
      }, 1000);
      //获取验证码
    },
    nextStep() {
      //验证手机号
      if (this.btnDisabled) return;
      this.$http
        .checkCode({
          data: {
            phone: this.formData.phone,
            code: this.formData.code
          }
        })
        .then(res => {
          this.$link(
            `/resetPwd?account=${this.formData.phone}&code=${this.formData
              .code}`
          );
        });
    }
  }
};
</script>




