<style lang="less" scoped>
@import "../../css/common.less";
.header {
  flex: 0 0 auto;
  height: unit(88/@rem, rem);
  background: transparent;
  padding: 0 unit(88/@rem, rem);
  position: relative;
  h1 {
    font-weight: 400;
    color: @txt-lv6;
    text-align: center;
    height: unit(88/@rem, rem);
    line-height: unit(88/@rem, rem);
    width: 100%;
  }
  .icon {
    width: unit(88/@rem, rem);
    height: unit(88/@rem, rem);
    padding: unit(22/@rem, rem);
    position: absolute;
    box-sizing: border-box;
    top: 0;
  }
  .left {
    left: 0;
  }
  .right {
    right: 0;
  }
}
</style>
<template>
    <header class="header flex row">
        <span class="left icon flex center">
            <slot name="left"></slot>
        </span>
        <h1 class="fz-36">{{title}}</h1>
        <span class="right icon flex center">
            <slot name="right"></slot>
        </span>
    </header>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    back: {
      type: Boolean,
      default: false
    }
  },
  methods: {}
};
</script>



